@import url(https://fonts.googleapis.com/css?family=Roboto);
/* 
  Variables
*/
:root{
  --beam-brand-blue: #00c9f0;
  --beam-brand-pink: #f31884;
  --beam-brand-chartreuse: #daff00;
}

body{
  font-family: 'Roboto', sans-serif;
}

.beam-brush-blue {
  background-color: #00c9f0;
  background-color: var(--beam-brand-blue);
}

.beam-brush-pink {
  background-color: #f31884;
  background-color: var(--beam-brand-pink);
}

.beam-brush-chartreuse {
  background-color: #daff00;
  background-color: var(--beam-brand-chartreuse);
}

.beam-font-family {
  /*
    If you'd like to use Roboto, visit:
    https://fonts.google.com/specimen/Roboto?selection.family=Roboto
  */
  font-family: 'Roboto', sans-serif;
}

.beam-font-color {
  color: #666666;
}

.beam-title {
  font-weight: 100;
  margin-bottom: 16px;
  margin-top: 5px;
  text-align: center;
}
.beam-title h3{
  margin-top: 15px;
}

.beam-label {
  font-size: 20px;
  font-weight: 300;
  margin-bottom: 16px;
  line-height: 1.2em;
}

.beam-text {
  font-size: 16px;
  font-weight: 100;
  line-height: 1.2em;
}

.member-info-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 60px;
}

.member-name{
  text-align: center;
}

.shipping-details input{
  display: block;
  margin-top: 3px;
  margin-bottom: 10px;
  height: 1.4em;
}


.shipping-details label:focus{
  border: 1px solid #00c9f0;
  border: 1px solid var(--beam-brand-blue);
}

.brush-preferences{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 60px;
  margin-bottom: 20px;
}


.brush-preferences__pref-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.brush-preferences__values{
  margin-left: 20px;
}

.brush-preferences__color-container{
  height: 50px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.brush-preferences__brush-color-label{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 50px;
}

.brush-preferences__brush-color-icon{
  height: 40px;
  width: 40px;
  border: 2px solid black;
  position: absolute;
  left: 110%;
  top:0;
}


.member-details__loading{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.member-details__loading-icon {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.member-details__loading-icon:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #00c9f0 transparent #00c9f0 transparent;
  border-color: var(--beam-brand-blue) transparent var(--beam-brand-blue) transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.section-header{
  text-align: center;
  font-weight: bold;
}

#loading-error{
  color: red;
  text-align: center;
  font-weight: bold;
}
